<template>
  <v-container class="container">
    
    <!-- *********** Logo ************** -->
    <section class="d-flex justify-center pt-5 pb-5 bg-custom elevation-2 header">
      <img
        height="80"
        src="../assets/rio-mare-logo.png"
      >
    </section>

    <!-- *************  Filiera tracciata    *************** -->
    <section class="d-flex flex-column pa-5 mt-30">
      <h1 class="text-center text-custom"><b>TRACCIABILITA'</b></h1>
      <p class="mt-3 ma-0">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam luctus dictum porttitor. 
        Praesent quis diam commodo, euismod mi id, ullamcorper neque. Pellentesque vel suscipit turpis, 
        ut consectetur diam.</p>
    </section>

    <!-- ***************  Select Product    **************** -->
    <section class="pa-5 pt-2">
      <label for="">
        Seleziona il prodotto che stai scansionando:
      </label>
      <v-select
      class="mt-2"
          v-model="product"
          :items="items"
          :rules="rules"
          label="Seleziona prodotto"
          item-text="name"
          item-value="id"
          solo
        ></v-select>
    </section>


    <!--  ******************   Button Footer    ********************* -->
    <section class="pl-5 pr-5 button-footer">
      <v-row>
      <v-col cols="12">
        <!-- <router-link to="/scan_code"> -->
        <v-btn
          block
          color="primary"
          elevation="5"
          large
          @click="next"
          :disabled="!product"
        >
        Scansiona Codice
        </v-btn>
        <!-- </router-link> -->
      </v-col>
      <v-col cols="12" style="padding: 0">
        <p class="text-center ma-0">
          <b>o</b>
        </p>
      </v-col>
      <v-col cols="12">
        <v-btn
          block
          color="primary"
          elevation="5"
          large
          :disabled="!product"
          @click="insertManualData"
        >
        Inserisci manualmente
        </v-btn>

      </v-col>
    </v-row>
    </section>
    
  </v-container>
</template>

<script>
import axios from 'axios'

  export default {
    name: 'HomeComponent',
    data () {
      return {
        product: null,
        products: [],
        rules: [
          v => !!v || 'Seleziona un prodotto',
        ]
      }
    },
    computed: {
      items () {
        return this.products
      }
    },
    async mounted () {
      sessionStorage.removeItem('product')
      const res = await axios.get('https://backend-bolton.herokuapp.com/products')
      this.products = res.data
    },
    methods: {
        next () {
          sessionStorage.setItem('product', this.product)
          this.$router.push('/scan_code')
        },
         insertManualData () {
          sessionStorage.setItem('product', this.product)
          this.$router.push('/insert_code')
        }
      }
  }
</script>
