<template>
  <v-container>
    
    <!-- *********** Header ************** -->
    <section>
      <v-app-bar
          class="app-bar"
          elevation="4"
          color="#FABFAD"
      >
      <img
        height="30"
        src="../assets/rio-mare-logo.png"
      >
     
      <v-spacer></v-spacer>

   <router-link to="/">
      <v-btn icon style="color: #01184d">
        <v-icon>mdi-home</v-icon>
      </v-btn>
    </router-link>
      </v-app-bar>
    </section>

    <section>
      <canvas style="display: none" id="myCanvas" width="640" height="720"></canvas>
      <canvas style="display: none" id="canvasOutput" ></canvas>
       <canvas style="display: none" id="canvasOutput2" ></canvas>
    </section>

    <!-- *************  Body    *************** -->
    <section v-if="loaded && (!resultOK && !resultKO)">
    <section class="pr-5 pl-5 mt-15">
      <p>
        Di seguito trovi il risultato della lettura da parte del nostro servizio intelligente.
      </p>
      <p class="mt-2">
        Confidenza dell'elaborazione:
      </p>
      <h1 class="text-center" :class="{'text-success': confTot >= 75, 'text-danger': confTot < 75 && confTot >= 50, 'text-error': confTot < 50 }" style="font-size: 28px">
        <b>
          {{ confTot }}%
        </b>
      </h1>
      <p>
        Ti chiediamo di confermare la lettura fatta, e modificarla se necessario.
      </p>
    </section>

    <section class="pl-5 pr-5">
      <v-row>
        <v-col cols="8">
          <p><b>Dati Rilevati </b></p>
        </v-col>
        <v-col cols="4">
         <p> <b>Confidenza</b> </p>
        </v-col>
      </v-row>
      <v-row class="pl-3">
        <v-col cols="8" class="pa-0">
          <v-text-field
            label="Codice Lotto"
            placeholder="Inserisci codice lotto"
            :rules="rules"
            outlined
            v-model="codiceLotto"
          ></v-text-field>
        </v-col>
        <v-col cols="4" class="pa-0 d-flex justify-center mt-2" :class="{'text-success': codlottoConf >= 90, 'text-danger': codlottoConf < 90 && codlottoConf >= 70, 'text-error': codlottoConf < 70 }">
          <h2><b>{{ codlottoConf }}%</b></h2>
        </v-col>
      </v-row>
      <v-row class="pl-3">
        <v-col cols="8" class="pa-0">
          <v-text-field
            label="Linea Produzione"
            placeholder="Inserisci linea produzione"
            v-model="lineaProduzione"
            :rules="rules"
            outlined
          ></v-text-field>
        </v-col>
        <v-col cols="4" class="pa-0 d-flex justify-center mt-2" :class="{'text-success': lineaProdConf >= 90, 'text-danger': lineaProdConf < 90 && lineaProdConf >= 70, 'text-error': lineaProdConf < 70 }">
          <h2><b> {{ lineaProdConf }}%</b></h2>
        </v-col>
      </v-row>
          <v-row class="pl-3">
            <v-col cols="8" class="pa-0">
              <v-text-field
            label="Ora Produzione"
            placeholder="Inserisci ora produzione"
            v-model="oraProduzione"
            :rules="rules"
            outlined
          ></v-text-field>
            </v-col>
            <v-col cols="4" class="pa-0 d-flex justify-center mt-2" :class="{'text-success': oraProdConf >= 90, 'text-danger': oraProdConf < 90 && oraProdConf >= 70, 'text-error': oraProdConf < 70 }">
              <h2><b>{{ oraProdConf }}%</b></h2>
            </v-col>
          </v-row>
    </section>

    <!--  ******************   Button Footer    ********************* -->
    <section style="text-align: center" class="pl-5 pr-5 pt-5 button-footer">
      <v-row>
      <v-col cols="12">
        <v-btn
          block
          color="primary"
          elevation="5"
          large
          @click="verifyTraceability"
          :disabled="!codiceLotto || !oraProduzione || !lineaProduzione "
        >
        Scopri la tracciabilità
        </v-btn>
      </v-col>
    </v-row>
    </section>
    </section>

    <section v-if="!loaded" style="height: 75vh" class="d-flex justify-center align-center">
      <v-progress-circular
      :size="70"
      :width="7"
      color="primary"
      indeterminate
    ></v-progress-circular>

    </section>

    <!-- *********************  Traceability result OK ***************** -->
    <section v-if="resultOK && loaded" style="height: 75vh" class="d-flex justify-center align-center">
      <v-card
         color="success"
         elevation="2"
         class="mx-auto pa-5 mt-10"
         style="position: relative"
      >
        <div style="position: absolute; top: 10px; right: 10px;">
          <v-btn
              icon
              color="white"
               @click="closeAlert"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
        </div>
        <v-card-title class="mt-3">
          <p  class="text-center" style="white-space:pre-wrap; word-break:break-word;color:white !important">
            La tracciabilità del prodotto selezionato è stata verificata con successo
          </p> 
            
        </v-card-title>
      </v-card>

    </section>

    <!-- *********************  Traceability result KO ***************** -->
    <section v-if="resultKO && loaded" style="height: 75vh" class="d-flex justify-center align-center">
      <v-card
         color="error"
         elevation="2"
         class="mx-auto pa-5 mt-10"
         style="position: relative"
      >
        <div style="position: absolute; top: 10px; right: 10px;">
          <v-btn
              icon
              color="white"
              @click="closeAlert"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
        </div>
        <v-card-title class="mt-3">
          <p  class="text-center" style="white-space:pre-wrap; word-break:break-word;color:white !important">
            La tracciabilità del prodotto selezionato non è stata verificata. 
            Ti chiediamo di verificare i dati scansionati e/o inseriti e riprovare 
          </p> 
            
        </v-card-title>
      </v-card>
    </section>
    
  </v-container>
</template>

<script>
import axios from 'axios'
import * as cv from 'opencv.js'
  export default {
    name: 'TraceabilityComponent',
    data () {
      return {
        file: null,
        loaded: false,
        codiceLottoConfidenza: 0,
        codiceLotto: '',
        lineaProduzioneConfidenza: 0,
        lineaProduzione: '',
        oraProduzioneConfidenza: 0,
        oraProduzione: '',
        prod_res: null,
        code: null,
        resultOK: false,
        resultKO: false,
         errorCount: 0,
         previousCode: null,
         previousProduction: null,
         previousTime: null,
         rules: [
          v => !!v || 'Inserisci manualmente il valore',
        ]
      }
    },
    async mounted () {
      let fileBase64 = sessionStorage.getItem('code')

      //  var link = document.createElement('a')
      //   link.download = 'originalImage';
      //   link.href = fileBase64
      //   link.click()

      let img = new Image()
      var ImageURL = fileBase64
      var block = ImageURL.split(";");
      var realData = block[1].split(",")[1];

      var blob = this.b64toBlob(realData, "image/jpeg");
      img.src = URL.createObjectURL( blob )

      var that = this
      img.onload = async function() {
      var canvas = document.getElementById("myCanvas");
      var ctx = canvas.getContext("2d");        
      ctx.drawImage(img, 0, 0, 1280, 720);
      
      let imageFixed = that.fixColorImage(img)
      let newFileBase64 = imageFixed

        let codeSession = sessionStorage.getItem('product')
        const prod_res = await axios.get('https://backend-bolton.herokuapp.com/products/' + codeSession)
        // that.prod_res = await axios.get('http://localhost:4000/products/' + codeSession)
        const code = prod_res.data.productList
        const productDetails = prod_res.data.name
       
        axios.post('https://backend-bolton.herokuapp.com/imageProcessing', {
          // axios.post('http://localhost:4000/imageProcessing', {
          base64: newFileBase64,
          code: code,
          productDetails: productDetails
        })
        .then((res) => {
          let result = res.data
          that.codiceLottoConfidenza = result.codiceLottoConfidenza,
          that.codiceLotto = result.codiceLotto,
          that.lineaProduzioneConfidenza = result.lineaProduzioneConfidenza,
          that.lineaProduzione = result.lineaProduzione,
          that.oraProduzioneConfidenza = result.oraProduzioneConfidenza,
          that.oraProduzione = result.oraProduzione
          that.loaded = true
        })
        .catch((error) => {
          console.log(error)
          that.loaded = true
        })

      
      }
    
    },
    computed: {
     codlottoConf () {
        return Math.trunc((this.codiceLottoConfidenza))
      },
      lineaProdConf () {
        return Math.trunc(this.lineaProduzioneConfidenza)
      },
      oraProdConf () {
        return Math.trunc(this.oraProduzioneConfidenza)
      },
      confTot () {
        let sum = (this.lineaProduzioneConfidenza + this.codiceLottoConfidenza + this.oraProduzioneConfidenza)
        if (sum > 0) {
          return Math.round((sum / 3))
        } else {
          return 0
        }
      }
    },
    methods: {
      fixColorImage (originalImage) {
        let mat = cv.imread(originalImage)
        let dst = new cv.Mat();
        let dst_new = new cv.Mat();
        cv.cvtColor(mat, dst, cv.COLOR_RGBA2RGB, 0);
        cv.cvtColor(dst, dst_new, cv.COLOR_RGB2Lab, 0);
        let labPlanes = new cv.MatVector()
        let mergedPlanes = new cv.MatVector()
        let new_dst = new cv.Mat();
        cv.split(dst_new, labPlanes)
        const B = labPlanes.get(2)
        mergedPlanes.push_back(B)
        cv.merge(mergedPlanes, new_dst)
        cv.imshow('canvasOutput', new_dst);
        
        
        var link = document.createElement('a')
        link.download = 'save';
        link.href = document.getElementById('canvasOutput').toDataURL('image/jpeg')
        
        mat.delete();
        dst.delete();
        new_dst.delete();
        mergedPlanes.delete();
        labPlanes.delete();
        // link.click()
        return link.href
      },
      b64toBlob(b64Data, contentType, sliceSize) {
        contentType = contentType || '';
        sliceSize = sliceSize || 512;

        var byteCharacters = atob(b64Data);
        var byteArrays = [];

        for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
            var slice = byteCharacters.slice(offset, offset + sliceSize);

            var byteNumbers = new Array(slice.length);
            for (var i = 0; i < slice.length; i++) {
                byteNumbers[i] = slice.charCodeAt(i);
            }

            var byteArray = new Uint8Array(byteNumbers);

            byteArrays.push(byteArray);
        }

      var blob = new Blob(byteArrays, {type: contentType});
      return blob;
      },
      verifyTraceability () {
        this.loaded = false

        let errorFinds = sessionStorage.getItem('errorCount')
            if (errorFinds) {
              this.errorCount = errorFinds
            } else {
              this.errorCount = 0
              sessionStorage.setItem('errorCount', 0)
            }
            let previousCode = sessionStorage.getItem('previousCode')
            let previousProduction = sessionStorage.getItem('previousProduction')
            let previousTime = sessionStorage.getItem('previousTime')

            if (previousCode.toUpperCase() != this.codiceLotto.toUpperCase()) {
              sessionStorage.setItem('previousCode', this.codiceLotto.toUpperCase())
              sessionStorage.setItem('errorCount', 0)
              this.errorCount = 0
            }
            if (previousProduction.toUpperCase() != this.lineaProduzione.toUpperCase()) {
              sessionStorage.setItem('previousProduction', this.lineaProduzione.toUpperCase())
              sessionStorage.setItem('errorCount', 0)
              this.errorCount = 0
            }
            if (previousTime != this.oraProduzione) {
              sessionStorage.setItem('previousTime', this.oraProduzione )
              sessionStorage.setItem('errorCount', 0)
              this.errorCount = 0
            }

          axios.post('https://backend-bolton.herokuapp.com/traceability', {
          product_id: 1,
          description: this.prod_res.data.name,
          weight: this.prod_res.data.weight,
          lot: this.codiceLotto,
          code: this.lineaProduzione,
          production_hours: this.oraProduzione
        })
        .then( () => {
          this.loaded = true
          this.resultOK = true
          this.resultKO = false
        } ) 
        .catch( (err) => {
           if (err.response.status == 404) {
            this.loaded = true
            this.resultKO = true
            this.resultOK = false
            this.errorCount++
            sessionStorage.setItem('errorCount', this.errorCount)  
            if (this.errorCount > 1 ) {
              this.$router.push('/email')
            }
          }
        })
      },
      closeAlert () {
        this.resultKO = false
        this.resultOK = false
      }
    }

    
  }
</script>
