import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import ScanCode from '../views/ScanCode.vue'
import InsertCode from '../views/InsertCode.vue'
import CustomizePicture from '../views/CustomizePicture.vue'
import TraceabilityResult from '../views/TraceabilityResult.vue'
import Email from '../views/Email.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/scan_code',
    name: 'ScanCode',
    component: ScanCode
  },
  {
    path: '/insert_code',
    name: 'InsertCode',
    component: InsertCode
  },
  {
    path: '/customize_picture',
    name: 'CustomizePicture',
    component: CustomizePicture
  },
  {
    path: '/traceability_result',
    name: 'TraceabilityResult',
    component: TraceabilityResult
  },
  {
    path: '/email',
    name: 'Email',
    component: Email
  }
]

const router = new VueRouter({
  routes
})

export default router
